<template>
  <main>
    <div id="find" v-if="!show.email && !show.password">
      <!-- <div id="find-email" @click="show.email = true">
        <p> </p>
      </div> -->
      <div id="find-password" @click="show.password = true">
        <p> </p>
      </div>
    </div>
    <div class="email" v-if="show.email">
      <div v-if="!show.emailResult" class="content">
        <p class="title"> </p>
        <p class="tip">{{tip.code}}</p>
        <input @input="checkCode(find, tip)" v-model="find.code" type="text" placeholder=" " onfocus="this.select()">
        <div class="btn-field">
          <button @click="findEmail(find, show, result, proxy.axios)" :disabled="tip.code.length > 0 || find.code.length <= 0"> </button>
          <button @click="clear()"></button>
        </div>
      </div>
      <div class="result" v-else>
        <div class="content">
          <p class="title"> </p>
          <p class="find">{{result.email}}</p>
          <div class="result-field">
            <router-link to="/login"> </router-link>
            <button @click="clear()"></button>
          </div>
        </div>
      </div>
    </div>
    <div class="password" v-if="show.password && result.passwordChangeCode.length <= 0 && !show.passwordChangeCodeCheck">
      <div class="content">
        <p class="title"> </p>
        <p class="tip">{{tip.email}}</p>
        <input @input="checkEmail(find, tip)" v-model="find.email" type="text" placeholder=" " onfocus="this.select()">
        <p class="tip">{{tip.code}}</p>
        <input @input="checkCode(find, tip)" v-model="find.code" type="text" placeholder=" " onfocus="this.select()">
        <div class="btn-field">
          <button @click="getPasswordChangeCode(find, result, proxy.axios)" :disabled="tip.code.length + tip.email.length > 0 || find.email.length <= 0 || find.code.length <= 0"> </button>
          <button @click="clear()"></button>
        </div>
      </div>
    </div>
    <div class="password-change-code" v-if="show.password && result.passwordChangeCode.length > 0 && !show.passwordChangeCodeCheck">
      <div class="content">
        <p class="title"> </p>
        <input v-model="find.passwordChangeCode" type="text" placeholder="   ">
        <div class="btn-field">
          <button @click="checkPasswordChangeCode(find, result, show)" :disabled="find.passwordChangeCode.length <= 0"></button>
          <button @click="clear()"></button>
        </div>
      </div>
    </div>
    <div class="password-change" v-if="show.password && show.passwordChangeCodeCheck">
      <div class="content">
        <p class="title"> </p>
        <p class="tip">{{tip.password}}</p>
        <input @input="checkPassword(find, tip)" v-model="find.password" type="password" placeholder="  ">
        <p class="tip">{{tip.checkPassword}}</p>
        <input @input="checkCheckPassword(find, tip)" v-model="find.checkPassword" type="password" placeholder="     ">
        <div class="btn-field">
          <button @click="changePassword(find, proxy.axios)" :disabled="tip.password.length + tip.checkPassword.length > 0 || find.password.length <= 0 || find.checkPassword.length <= 0"> </button>
          <button @click="clear()"></button>
        </div>
      </div>
    </div>
    <router-link to="/" class="main-page">  </router-link>
    <router-link to="/login" class="login-page">  </router-link>
    <div class="loading">
      <div class="loading-content">
        <h1>LOADING</h1>
        <p>   .  </p>
      </div>
    </div>
  </main>
</template>

<script>
import { ref, getCurrentInstance, onBeforeMount } from 'vue';
import { checkCode, checkEmail, checkPassword, checkCheckPassword } from '../../../js/util/validation.js';
import { findEmail, getPasswordChangeCode, checkPasswordChangeCode, changePassword } from '../../../js/community/user/findAccount.js';
import { removeSessionStorage } from '../../../js/util/value.js';

export default {
  setup() {
    const { proxy } = getCurrentInstance();

    const find = ref({
      email: '',
      code: '',
      passwordChangeCode: '',
      password: '',
      checkPassword: '',
    });

    const show = ref({
      email: false,
      password: false,
      emailResult: false,
      passwordChangeCodeCheck: false,
    });

    const tip = ref({
      email: '',
      code: '',
      password: '',
      checkPassword: '',
    });

    const result = ref({
      email: '',
      passwordChangeCode: '',
      expireTime: '',
    });

    const clear = () => {
      location.reload();
    };

    onBeforeMount(() => {
      if(window.sessionStorage.getItem('jwt-auth-token') !== null) {
        alert('    ');
        location.href = '/';
      }
    });

    return { find, show, tip, result, proxy, findEmail, getPasswordChangeCode, removeSessionStorage, checkPasswordChangeCode, changePassword, checkPassword, checkCheckPassword, checkCode, checkEmail, clear };
  }
}
</script>

<style scoped src="../../../css/community/user/findAccountComponent.css" />