export { pageDivision, pagination };
//  
function pageDivision(arr, divisionPage) { // ,      
  let totalPage = []; //   
  let tempArr = []; //   
  let maxPage = Math.ceil(arr.length / divisionPage); //     
  
  for(let i = 1; i <= maxPage; i++) { //1  
    tempArr.push(i); //    

    if(i % 5 === 0 || i === maxPage) { //      i  i   
      totalPage.push(tempArr); //     
      tempArr = []; // 
    }
  }
  
  return totalPage;
}
//
function pagination(arr, currentPage, divisionPage) { // ,  ,      
  let currentView = []; //   
  currentPage = (currentPage - 1) * divisionPage; //  

  if(currentPage > arr.length) currentPage = 0;

  for(let i = currentPage; i < currentPage + divisionPage; i++) { //      
    currentView.push(arr[i]); //      
    
    if(i >= arr.length - 1) break; //  i   
  }

  return currentView;
}