export { checkPassword, checkCheckPassword, checkName, checkCode, checkEmail };

const specialPattern = /[!@#$%^&*()_+={}:;<>?/|.,`~'"[\]\\-]/;
const includePattern = /[!@#$%^&*?.]/;
const decludePattern = /[()_+={}:;<>/|,`~'"[\]\\-]/;
const numberPattern = /[0-9]/;
const emailPattern = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/;

function checkPassword(value, tip) {
  if(value.password.length !== 0) {
    if(value.password.length < 8 || value.password.length > 20) tip.password = ' 8 , 20  .';
    else if(value.password.search(/\s/) != -1) tip.password = '   .';
    else if(decludePattern.test(value.password)) tip.password = '    .';
    else if(!includePattern.test(value.password)) tip.password = '!,@,#,$,%,^,&,*,?,.   .';
    else if(numberPattern.test(value.password.split('')[0])) tip.password = '   .';
    else tip.password = '';
  } else tip.password = '';
}

function checkCheckPassword(value, tip) {
  if(value.checkPassword.length !== 0) {
    if(value.checkPassword !== value.password) tip.checkPassword = '  .';
    else tip.checkPassword = '';
  } else tip.checkPassword = '';
}

function checkName(value, tip) {
  if(value.name.length !== 0) {
    if(value.name.length > 20) tip.name = ' 20  .';
    else if(value.name.search(/\s/) != -1) tip.name = '   .';
    else if(decludePattern.test(value.name)) tip.name = '    .';
    else tip.name = '';
  } else tip.name = '';
}

function checkCode(value, tip) {
  if(value.code.length !== 0) {
    if(value.code.length !== 9) tip.code = ' 9.';
    else if(value.code.search(/\s/) != -1) tip.code = '   .';
    else if(specialPattern.test(value.code)) tip.code = '    .';
    else tip.code = '';
  } else tip.code = '';
}

function checkEmail(value, tip) {
  if(!emailPattern.test(value.email) && value.email.length > 0) tip.email = '  ';
  else tip.email = '';
}