<template>
  <main>
    <div id="login">
      <p class="title">LOGIN PAGE</p>
      <div class="input-field">
        <input @keyup.enter="login(loginInfo, proxy.axios)" v-model="loginInfo.email" type="text" class="id" placeholder=" " onfocus="this.select()">
        <input @keyup.enter="login(loginInfo, proxy.axios)" v-model="loginInfo.password" type="password" class="password" placeholder=" " onfocus="this.select()">
        <button @click="login(loginInfo, proxy.axios)"></button>
      </div>
      <div class="advice">
        <router-link to="/register">  ?</router-link>
        <router-link to="/find_account">/ ?</router-link>
      </div>
    </div>
    <router-link to="/" class="main-page">  </router-link>
  </main>
</template>

<script>
import { ref, getCurrentInstance, onBeforeMount } from 'vue';
import { login } from '../../../js/community/user/user.js';

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    
    const loginInfo = ref({
      email: '',
      password: '',
    });

    onBeforeMount(() => {
      if(window.sessionStorage.getItem('jwt-auth-token') !== null) {
        alert('    ');
        location.href = '/';
      }
    });

    return { loginInfo, proxy, login };
  }
}
</script>

<style scoped src="../../../css/community/user/loginComponent.css" />