<template>
  <main>
    <nav class="select-stage">
      <router-link to="" class="select">
        
        <div class="step">
          <div class="step-area">
            <p class="world 1" @click="selectStage($event)">1</p>
            <p class="world 2" @click="selectStage($event)">2</p>
            <p class="world 3" @click="selectStage($event)">3</p>
          </div>
        </div>
      </router-link>
      <router-link to="">
        
        <div class="step">
          <div class="step-area">
            <p class="future 1" @click="selectStage($event)">1</p>
            <p class="future 2" @click="selectStage($event)">2</p>
            <p class="future 3" @click="selectStage($event)">3</p>
          </div>
        </div>
      </router-link>
      <router-link to="">
        
        <div class="step">
          <div class="step-area">
            <p class="space 1" @click="selectStage($event)">1</p>
            <p class="space 2" @click="selectStage($event)">2</p>
            <p class="space 3" @click="selectStage($event)">3</p>
          </div>
        </div>
      </router-link>
      <router-link to="">
         
        <div class="step">
          <div class="step-area">
            <p class="legend 1" @click="selectStage($event)">1</p>
            <p class="legend 2" @click="selectStage($event)">2</p>
            <p class="legend 3" @click="selectStage($event)">3</p>
            <p class="legend 3" @click="selectStage($event)">4</p>
          </div>
        </div>
      </router-link>
      <router-link to="">
         
        <div class="step">
          <div class="step-area">
            <p class="origin-legend 1" @click="selectStage($event)">1</p>
            <p class="origin-legend 2" @click="selectStage($event)">2</p>
          </div>
        </div>
      </router-link>
      <router-link to="" class="event non-step" @click="selectStage($event)"> </router-link>
      <router-link to="" class="collabo non-step"> </router-link>
      <input type="text" class="search" @input="searchStage($event)" placeholder="">
      <img src="" alt="">
    </nav>
    <nav class="event-stage" v-if="stage === 'event' || stage === 'guerrilla' || stage === 'week' || stage === 'drop' || stage === 'advent' || stage === 'crazy' || stage === 'cyclone' || stage === 'arousal' || stage === 'excavation' || stage === 'lesson'">
      <router-link to="" class="event non-step select" @click="selectEventStage($event)">
         
      </router-link>
      <router-link to="" class="week">
        / 
        <div class="step">
          <div class="step-area">
            <p class="guerrilla" @click="selectEventStage($event)"></p>
            <p class="sunday" @click="selectEventStage($event)"></p>
            <p class="monday" @click="selectEventStage($event)"></p>
            <p class="tuesday" @click="selectEventStage($event)"></p>
            <p class="wednesday" @click="selectEventStage($event)"></p>
            <p class="thursday" @click="selectEventStage($event)"></p>
            <p class="friday" @click="selectEventStage($event)"></p>
            <p class="saturday" @click="selectEventStage($event)"></p>
          </div>
        </div>
      </router-link>
      <router-link to="" class="drop" @click="selectEventStage($event)">
         
        <div class="step">
          <div class="step-area">
            <p class="drop" @click="selectEventStage($event)"></p>
            <p class="advent" @click="selectEventStage($event)"></p>
            <p class="crazy" @click="selectEventStage($event)"></p>
            <p class="cyclone" @click="selectEventStage($event)"></p>
            <p class="arousal" @click="selectEventStage($event)"></p>
          </div>
        </div>
      </router-link>
      <router-link to="" class="excavation" @click="selectEventStage($event)">
         
        <div class="step">
          <div class="step-area">
            <p class="high" @click="selectEventStage($event)"></p>
            <p class="common" @click="selectEventStage($event)"></p>
            <p class="crude" @click="selectEventStage($event)"></p>
          </div>
        </div>
      </router-link>
      <router-link to="" class="lesson non-step" @click="selectEventStage($event)">
        
      </router-link>
    </nav>
    <content-component v-if="searchData.length === 0" :stageData="stageData" :step="step" />
    <search-component v-else :searchData="searchData" :step="step" />
  </main>
</template>

<script>
import { onBeforeMount } from 'vue';
import contentComponent from "./StageInfoContentComponent.vue";
import searchComponent from './StageInfoSearchComponent.vue';

export default {
  setup() {
    onBeforeMount(() => {
      alert('');
      location.href = '/';
    });
  },
  data() {
    return {
      stageData: [],
      searchData: [],
      stage: "world",
      step: 1,
    };
  },
  async mounted() {
    let { data } = await this.axios.get(`/stage_data_world`);
    this.stageData = data;
  },
  methods: {
    async selectStage(event) {
      let stageName = event.currentTarget.className;
      let menu = event.currentTarget.closest('nav');

      menu.childNodes[7].value = '';
      this.searchData = [];
      
      for(let i = 0; i < 6; i++) menu.childNodes[i].classList.remove('select');
      event.currentTarget.closest('a').classList.add('select');

      if (stageName.includes("world")) {
        let { data } = await this.axios.get(`/stage_data_world`);
        this.stageData = data;
        this.stage = "world";
      } else if (stageName.includes("future")) {
        let { data } = await this.axios.get(`/stage_data_future`);
        this.stageData = data;
        this.stage = "future";
      } else if (stageName.includes("space")) {
        let { data } = await this.axios.get(`/stage_data_space`);
        this.stageData = data;
        this.stage = "space";
      } else if (stageName.includes("legend") && !stageName.includes('origin-legend')) {
        let { data } = await this.axios.get(`/stage_data_legend`);
        this.stageData = data;
        this.stage = "legend";
      } else if (stageName.includes("origin-legend")) {
        let { data } = await this.axios.get(`/stage_data_origin_legend`);
        this.stageData = data;
        this.stage = "origin-legend";
      } else if (stageName.includes("event")) {
        let { data } = await this.axios.get(`/stage_data_event`);
        this.stageData = data;
        console.log(data);
        this.stage = "event";
      } else if (stageName.includes("collabo")) {
        let { data } = await this.axios.get(`/stage_data_collabo`);
        this.stageData = data;
        this.stage = 'collabo';
      }

      if(stageName.includes('1')) {
        this.step = 1;
      } else if(stageName.includes('2')) {
        this.step = 2;
      } else if(stageName.includes('3')) {
        this.step = 3;
      } else if(stageName.includes('4')) {
        this.step = 4;
      }
    },
    async selectEventStage(event) {
      let stageName = event.currentTarget.className;
      let menu = event.currentTarget.closest('nav').childNodes;
      event.currentTarget.closest('main').childNodes[0].childNodes[7].value = ''
      this.searchData = [];

      for(let i = 0; i < menu.length; i++) menu[i].classList.remove('select');
      event.currentTarget.closest('a').classList.add('select');

      if (stageName.includes("event")) {
        let { data } = await this.axios.get(`/stage_data_event`);
        this.stageData = data;
        this.stage = "event";
      } else if(stageName.includes("guerrilla")) {
        let { data } = await this.axios.get(`/stage_data_guerrilla`);
        this.stageData = data;
        this.stage = "guerrilla";
      } else if(stageName.includes("day")) {
        let week = '';

        if(stageName === 'sunday') {
          week = ''
        } else if(stageName === 'monday') {
          week = '';
        } else if(stageName === 'tuesday') {
          week = '';
        } else if(stageName === 'wednesday') {
          week = '';
        } else if(stageName === 'thursday') {
          week = '';
        } else if(stageName === 'friday') {
          week = '';
        } else if(stageName === 'saturday') {
          week = '';
        }

        let { data } = await this.axios.get(`/stage_data_week/${week}`);
        this.stageData = data;
        this.stage = "week";
      } else if(stageName === 'drop') {
        let { data } = await this.axios.get(`/stage_data_drop`);
        this.stageData = data;
        this.stage = "drop";
      } else if(stageName === 'advent') {
        let { data } = await this.axios.get(`/stage_data_advent`);
        this.stageData = data;
        this.stage = "advent";
      } else if(stageName === 'crazy') {
        let { data } = await this.axios.get(`/stage_data_crazy`);
        this.stageData = data;
        this.stage = "crazy";
      } else if(stageName === 'cyclone') {
        let { data } = await this.axios.get(`/stage_data_cyclone`);
        this.stageData = data;
        this.stage = "cyclone";
      } else if(stageName === 'arousal') {
        let { data } = await this.axios.get(`/stage_data_arousal`);
        this.stageData = data;
        this.stage = "arousal";
      } else if(stageName === 'high') {
        let { data } = await this.axios.get(`/stage_data_excavation/${''}`);
        this.stageData = data;
        this.stage = "excavation";
      } else if(stageName === 'common') {
        let { data } = await this.axios.get(`/stage_data_excavation/${''}`);
        this.stageData = data;
        this.stage = "excavation";
      } else if(stageName === 'crude') {
        let { data } = await this.axios.get(`/stage_data_excavation/${''}`);
        this.stageData = data;
        this.stage = "excavation";
      } else if(stageName === 'lesson') {
        this.stage = "lesson";
      }
    },
    async searchStage(event) {
      let stageName = event.currentTarget.value;
      let sortation = '';

      if (this.stage === 'world') {
        sortation = '';
      } else if (this.stage === 'future') {
        sortation = '';
      } else if (this.stage === 'space') {
        sortation = '';
      } else if (this.stage === 'legend') {
        sortation = ' ';
      } else if (this.stage === 'origin-legend') {
        sortation = ' ';
      } else if (this.stage === 'event') {
        sortation = ' ';
      } else if(this.stage === 'guerrilla') {
        sortation = ' ';
      } else if(this.stage === 'week') {
        sortation = ' '
      } else if(this.stage === 'drop') {
        sortation = ' ';
      } else if(this.stage === 'advent') {
        sortation = ' ';
      } else if(this.stage === 'crazy') {
        sortation = ' ';
      } else if(this.stage === 'cyclone') {
        sortation = ' ';
      } else if(this.stage === 'arousal') {
        sortation = ' ';
      } else if (this.stage === 'excavation') {
        sortation = ' ';
      } else if (this.stage === 'lesson') {
        sortation = ' ';
      } else if (this.stage === 'collabo') {
        sortation = ' ';
      }

      let stageInfo = [sortation, stageName];
      if(stageName.length !== 0) {
        let { data } = await this.axios.get(`/stage_data_name/${stageInfo}`);
        this.searchData = data;
      } else {
        this.searchData = [];
      }
    }
  },
  components: {
    contentComponent,
    searchComponent,
  },
};
</script>

<style scoped>
main {
  width: 100%;
}

nav {
  width: 55%;
  margin: 0.5% auto;
  font-size: 1.5rem;
  display: grid;
  grid-template-rows: 120%;
}

.select-stage {
  grid-template-columns: repeat(8, 1fr);
}

.event-stage {
  grid-template-columns: repeat(5, 1fr);
}

nav a {
  border: 1px solid #000000;
  border-left: none;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

nav a:nth-child(1) {
  border-left: 1px solid #000000;
}

nav a:hover {
  color: #ffffff;
}

.non-step:hover {
  background-color: #ffc038;
}

nav a:hover .step {
  display: flex;
}

.select {
  color: #ffc038;
}

.step {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  lefT: 0;
  top: 0;
}

.step-area {
  width: 100%;
  height: 100%;
  display: flex;
}

.step-area p {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}

.event-stage > .week .step-area p {
  font-size: 1.1rem;
}

.event-stage > .drop .step-area p {
  font-size: 1.3rem;
}

.step-area p:hover {
  background-color: #ffc038;
  color: #ffffff;
}

input {
  border: 1px solid #000000;
  border-left: none;
  text-align: center;
  outline: 0;
}
</style>