<template>
  <section>
    <div id="schedule">
      <p class="schedule-title"> </p>
      <div class="title">
        <p></p>
        <p></p>
        <p> </p>
      </div>
      <div class="info" v-for="value in schedule.currentData" :key="value">
        <p :class="new Date(value.start) <= new Date(dateInfo.date) && new Date(value.end) >= new Date(dateInfo.date) ? 'select' : ''">{{value.content}}</p>
        <p :class="new Date(value.start) <= new Date(dateInfo.date) && new Date(value.end) >= new Date(dateInfo.date) ? 'select' : ''">{{value.start}}</p>
        <p :class="new Date(value.start) <= new Date(dateInfo.date) && new Date(value.end) >= new Date(dateInfo.date) ? 'select' : ''">{{value.end}}</p>
      </div>
    </div>
    <div class="pages">
      <p to="" @click="prevPage()">&laquo;</p>
      <p to="" :class="'page ' + `${pageInfo.currentPage === value ? 'select' : ''}`" v-for="value in pageInfo.totalPage[pageInfo.viewPage]" :key="value" @click="selectPage(value)">{{value}}</p>
      <p to="" @click="nextPage()">&raquo;</p>
    </div>
  </section>
</template>

<script>
import { ref, onBeforeMount, getCurrentInstance } from 'vue';
import { getEventSchedule } from '../../../js/etc/schedule/schedule.js';
import { pageDivision, pagination } from '../../../js/util/pagination.js';

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const date = new Date();

    const dateInfo = ref({
      date: date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
    });
    const schedule = ref({
      data: [], //   
      currentData: [], //  
    });
    const pageInfo = ref({
      totalPage: [], // 
      divisionPage: 5, //        
      currentPage: 1, // 
      viewPage: 0, // 
    });

    const nextPage = () => { // 
      if(pageInfo.value.totalPage.length - 1 > pageInfo.value.viewPage) { //      
        pageInfo.value.viewPage++; //   
        pageInfo.value.currentPage = pageInfo.value.totalPage[pageInfo.value.viewPage][0]; //          
        contentUpdate(); //    
      }
    };

    const prevPage = () => { // 
      if(0 < pageInfo.value.viewPage) { //  0 
        pageInfo.value.viewPage--; //  
        pageInfo.value.currentPage = pageInfo.value.totalPage[pageInfo.value.viewPage][0]; //          
        contentUpdate(); //    
      }
    };

    const selectPage = (page) => { //   
      pageInfo.value.currentPage = page; //     
      contentUpdate(); //    
    };

    const contentUpdate = () => { //    
      schedule.value.currentData = pagination(getEventSchedule(proxy.store), pageInfo.value.currentPage, pageInfo.value.divisionPage); //    
      pageInfo.value.totalPage = pageDivision(getEventSchedule(proxy.store), pageInfo.value.divisionPage); //        
    };

    onBeforeMount(() => { //dom  
      const loadData = setInterval(() => {
        if(getEventSchedule(proxy.store) !== undefined) {
          schedule.value.data = getEventSchedule(proxy.store);
          contentUpdate();
          clearInterval(loadData);
        }
      }, 100);
    });

    return { dateInfo, schedule, pageInfo, nextPage, prevPage, selectPage };
  },
}
</script>

<style scoped>
section {
  width: 100%;
  margin: 0 auto;
}

#schedule {
  width: 100%;
}

.schedule-title {
  width: 100%;
  border: 1px solid #000000;
  border-right: none;
  border-left: none;
  font-size: 2rem;
  text-align: center;
}

.select {
  color: #ffc038;
}

.title {
  width: 100%;
  display: grid;
  grid-template-columns: 2.5fr 1fr 1fr;
  text-align: center;
  font-size: 1.8rem;
  border-bottom: 1px solid #000000;
}

.info {
  width: 100%;
  display: grid;
  grid-template-columns: 2.5fr 1fr 1fr;
  text-align: center;
  align-items: center;
  font-size: 1.5rem;
}

.info p {
  margin: 2% 0;
  border-bottom: 1px solid #000000;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pages {
  width: 100%;
  font-size: 3.5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid #000000;
}

.page {
  font-size: 1.8rem;
}

.pages p {
  cursor: pointer;
}
</style>