<template>
  <main>
    <div class="wrap">
      <element-component />
      <section>
        <div class="content">
          <router-link :to="`/unitInfo/${value.id.split('-')[0]}`" class="unit-content" v-for="value in info.current" :key="value">
            <img :src="require(`./../../assets/res/unit/${value.image_dir}`)" alt="">
            <p>{{value.name.length > 10 ? value.name.substring(0, 9) + '..' : value.name}}</p>
          </router-link>
        </div>
        <div class="pages">
          <p class="prev" @click="prevPage()">&lt;</p>
          <p :class="'page ' + `${pageInfo.currentPage === value ? 'select' : ''}`" v-for="value in pageInfo.totalPage[pageInfo.viewPage]" :key="value" @click="selectPage(value)">{{value}}</p>
          <p class="next" @click="nextPage()">&gt;</p>
        </div>
      </section>
    </div>
    <router-link to="/" class="main-page">  </router-link>
  </main>
</template>

<script>
import { ref, getCurrentInstance, onBeforeMount, watchEffect } from 'vue';
import { getUnitInfo, getSearchUnitInfo } from '../../js/unit/unitInfo.js';
import { pagination, pageDivision } from '../../js/util/pagination.js';
import elementComponent from './UnitInfoElementComponent.vue'

export default {
  setup() {
    const { proxy } = getCurrentInstance();

    const info = ref({
      all: [], // 
      current: [], // 
    });

    const pageInfo = ref({
      totalPage: [], // 
      divisionPage: 27, //        
      currentPage: 1, // 
      viewPage: 0, //   index
    });
    //  index
    const nextPage = () => {
      if(pageInfo.value.totalPage.length - 1 > pageInfo.value.viewPage) { //         
        pageInfo.value.viewPage++; //   index 
        pageInfo.value.currentPage = pageInfo.value.totalPage[pageInfo.value.viewPage][0]; //   index         
        contentUpdate(); //    
      }
    };
    //  index
    const prevPage = () => {
      if(0 < pageInfo.value.viewPage) { //   index 0 
        pageInfo.value.viewPage--; //   index 
        pageInfo.value.currentPage = pageInfo.value.totalPage[pageInfo.value.viewPage][0]; //   index         
        contentUpdate(); //    
      }
    };
    //  
    const selectPage = (page) => {
      pageInfo.value.currentPage = page; // 
      contentUpdate(); //    
    };
    //    
    const contentUpdate = () => {
      info.value.current = pagination(info.value.all, pageInfo.value.currentPage, pageInfo.value.divisionPage); //    
      pageInfo.value.totalPage = pageDivision(info.value.all, pageInfo.value.divisionPage); //  
    };

    onBeforeMount(() => {
      proxy.store.commit('setSearchUnitInfo', []); //  
      info.value.all = getUnitInfo(proxy.store); //   
      contentUpdate(); //    
    });

    watchEffect(() => {
      if(getSearchUnitInfo(proxy.store) !== undefined) { //  
        if(getSearchUnitInfo(proxy.store).length > 0) { //  
          info.value.current = pagination(getSearchUnitInfo(proxy.store), pageInfo.value.currentPage, pageInfo.value.divisionPage); //     
          pageInfo.value.totalPage = pageDivision(getSearchUnitInfo(proxy.store), pageInfo.value.divisionPage); //  
        } else { //   
          info.value.current = []; //  
          pageInfo.value.totalPage = [1]; //   1 
        }
      } else if(info.value.all.length > 0) contentUpdate(); //        
    });

    return { pageInfo, info, nextPage, prevPage, selectPage };
  },
  components: {
    elementComponent,
  },
}
</script>

<style scoped src="../../css/unit/unitInfoComponent.css" />